@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700;800&family=Open+Sans:wght@300;400;600;700&family=Raleway:wght@300;400;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.container {
  width: 100%;
  height: 100%;
  padding: 0 2em;
  margin: 0 auto;
  font-family: "Open Sans";
}
@media (min-width: 1024px) {
  .container {
    max-width: 1352px;
  }
}

.nav {
  padding: 1em 0;
  position: absolute;
  top: 2.7em;
  left: 0;
  box-shadow: 0px 0.1875em 0.375em rgba(0, 0, 0, 0.1607843137);
  padding: 1.5em 0 1.5em 2em;
  padding-left: 2em;
  width: 100vw;
  display: none;
}
@media (min-width: 1024px) {
  .nav {
    display: block;
    width: 40%;
    position: static;
    box-shadow: none;
  }
}
@media (min-width: 1024px) {
  .nav__list {
    display: flex;
    justify-content: space-between;
  }
}
.nav__item {
  margin-top: 0.6em;
}
.nav__item:first-child {
  margin-top: 0;
}
@media (min-width: 1024px) {
  .nav__item {
    margin: 0;
  }
}
.nav__item-link {
  font-size: 11px;
  color: #a5a5a5;
  font-weight: bold;
  letter-spacing: 0.28em;
}
.nav__item-link:hover {
  color: #000000;
}
@media (min-width: 1024px) {
  .nav__item-link:hover {
    border-bottom: 0.36em solid #08a6e4;
    padding-bottom: 2.18em;
  }
}
.nav--visible {
  display: block;
}

.toggle {
  width: 20px;
  height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}
.toggle__item {
  height: 3px;
  border-radius: 2px;
  width: 100%;
  background-color: #07ace6;
}
.toggle__item-closed {
  position: absolute;
  transition: transform 0.3s ease-in-out;
}
.toggle__item-closed:first-of-type {
  transform: rotate(45deg);
}
.toggle__item-closed:nth-of-type(2) {
  opacity: 0;
}
.toggle__item-closed:last-of-type {
  transform: rotate(-45deg);
}
@media (min-width: 1024px) {
  .toggle {
    display: none;
  }
}

.header {
  width: 100%;
  box-shadow: 0px 0.1875em 0.375em rgba(0, 0, 0, 0.1607843137);
  position: relative;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo-link {
  font-family: "Raleway";
  font-weight: lighter;
  color: #08a6e4;
}